.container{
    height: 100vh;
    display: flex;
    justify-content:center ;
    align-items: center ;
    flex-direction: column;
    background: linear-gradient(#010516, #0c0149)
}

.title{
    font-size: 85px;
    color: #fff;
    animation: flipTitle 2s;
}

@keyframes flipTitle{
    from{
        transform: rotateX(90deg);
    }
    to{
        transform: rotateX(0deg);
    }
}


.containerInput{
    background-color: rgb(255, 255, 255, 0.2);
    padding: 15px;
    margin: 34px 0;
    border-radius: 8px;
    box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.5);
    display: flex;

    
}

.containerInput input{
    background-color: transparent;
    border: 0;
    font-size: 20px;
    color: #fff;
    outline: none;
    margin-right: 8px;
}

.containerInput input::placeholder{
    color: #f1f1f1;


}

.buttonpesquisar{
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: tranform 0.5s;
    

}

.buttonpesquisar:hover{
    transform: scale(1.5);
}


.main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
    width: 500px;
    border-radius: 9px;
}

.main h2{
    margin: 16px 0;
    font-size: 39px;
}

.main span{
    margin-bottom: 16px;
    font-weight: bold;
}

@media(max-width:620px){
    .title{
        font-size: 60px;
    }

    .main h2{
        font-size: 28px;
    }

    .main{
        width: 80%;
        min-height: 250px;
    }
}

.rodape a{
    display: flex;
    align-items: flex-end;
    color: #f1f1f1;
    text-decoration: none;
    gap: 5px;
    
}

.rodape .link{
    color: #ececee;
    font-weight: 600;
}